::-webkit-scrollbar {
  width: 4px;
  height: 4px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #0000; 
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #888; 
  border-radius: 10px;
  background: radial-gradient(96.34% 96.34% at 59.82% 3.66%, #4532B5 0%, #36405A 100%);
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: radial-gradient(96.34% 96.34% at 59.82% 3.66%, #4532B5 0%, #36405A 100%);
}

