    height: 31px;
    background: #49526C;
    border-radius: 56px;
    position: relative;
    z-index: 1;
    cursor: pointer;
    transition: .3s ease all;
}

.checkCircle {
    position: absolute;
    width: 24px;
    height: 24px;
    background: #FFFFFF;
    box-shadow: 0px 1px 5px rgba(0, 0, 0, 0.25);
    border-radius: 40px;
    top: 50%;
    left: 4px;
    transform: translateY(-50%);
    display: flex;
    align-items: center;
    justify-content: center;
    transition: .3s ease all;
}

.approveBtn input:checked + label {
    background: #08D70D;
}

.approveBtn input:checked + label .checkCircle {
    left: 27px;
}

.checkCircle:after {
    content: "";
    background-image: url(/static/media/CheckGray.816731c2.svg);
    width: 15px;
    height: 15px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
    transition: .3s ease all;
}

.approveBtn input:checked + label .checkCircle:after {
    background-image: url(/static/media/CheckGreen.99e97ac0.svg);
}

