.loader,
.loader:before,
.loader:after {
  border-radius: 50%;
  width: 1em;
  height: 1em;
  animation-fill-mode: both;
  animation: load7 1.8s infinite ease-in-out;
}
.loader {
    top: 50px;
    transition: calc(0.5);
    color: #3E64ED;
    font-size: 10px;
    margin: 80px auto;
    position: relative;
    text-indent: -9999em;
    transform: translateZ(0);
    animation-delay: -0.16s;
}
.loader:before,
.loader:after {
  content: '';
  position: absolute;
  top: 0;
}
.loader:before {
  left: -1.5em;
  animation-delay: -0.32s;
}
.loader:after {
  left: 1.5em;
}
@keyframes load7 {
  0%,
  80%,
  100% {
    box-shadow: 0 2.5em 0 -1.3em;
  }
  40% {
    box-shadow: 0 2.5em 0 0;
  }
}

.approve-loader__container__text{
    position: absolute;
    top: 46%;
    left: 42%;
    color: white;
}

.approve-loader,
.approve-loader:after {
    /* z-index: 200; */
    border-radius: 50%;
    width: 20em;
    height: 20em;
}
.approve-loader {
  margin: 60px auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: .5em solid rgba(255, 255, 255, 0.2);
  border-right: .5em solid rgba(255, 255, 255, 0.2);
  border-bottom: .5em solid rgba(255, 255, 255, 0.2);
  border-left: .5em solid #ffffff;
  transform: translateZ(0);
  animation: load8 2.1s infinite linear;
}
@keyframes load8 {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.approve-modal .modal-dialog .modal-content{
    background: none !important;
    border: none !important;
    position: absolute !important;
    top: 20% !important;
}

.changeNetwork-loader,
.changeNetwork-loader:before,
.changeNetwork-loader:after {
  border-radius: 50%;
  width: 2.5em;
  height: 2.5em;
  animation-fill-mode: both;
  animation: load7 1.8s infinite ease-in-out;
}
.changeNetwork-loader {
  color: #3E64ED;
  font-size: 4px;
  margin: 20px auto;
  position: relative;
  text-indent: -9999em;
  transform: translateZ(0);
  animation-delay: -0.16s;
}
.changeNetwork-loader:before,
.changeNetwork-loader:after {
  content: '';
  position: absolute;
  top: 0;
}
